@import 'modern-normalize/modern-normalize.css';



body {
  
 font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

ul,
ol {
  padding: 0;
  margin: 0;

  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
